@import url(https://fonts.googleapis.com/css?family=Poppins:200,400,600&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{font-size:15px;background-color:#fafafa}body{font-family:"Poppins",sans-serif}h1,h3{color:#4c4e61}.bold200{font-weight:200}.bold400{font-weight:400}.bold700{font-weight:700}p{color:#a3a5ae}header{line-height:1.7;margin:0 0 80px 0}header h1{font-size:25px;text-align:center;margin:0 0 10px 0}@media screen and (min-width: 1126px){header h1{font-size:35px}}header h1 span{display:block}header p{text-align:center;font-size:17px}main .items{line-height:1.7}main .items h2{font-size:22px}#root{padding:88px 34px}@media screen and (min-width: 1100px){#root{padding:90px 163px}}

@media screen and (min-width: 1126px){header{width:506px;height:220px;margin:0 auto}}

main{display:flex;flex-direction:column;flex-wrap:wrap;justify-content:center;max-width:1440px;margin:0 auto}@media screen and (min-width: 1126px){main{flex-direction:row;position:relative;height:600px}}main .items{height:221px;max-width:350px;background-color:white;padding:27px;line-height:1.6;position:relative;margin:0 auto 25px auto;border-radius:6px;border-top:solid black 5px;align-items:center;box-shadow:0px 8px 18px 3px rgba(0,0,0,0.13)}@media screen and (min-width: 1126px){main .items{position:absolute;width:26.5%}}main .items.blue{border-top-color:#549ef2}@media screen and (min-width: 1126px){main .items.blue{top:0;right:0;bottom:0;margin:auto 0;z-index:1000}}main .items.orange{border-color:#fcaf4a}@media screen and (min-width: 1126px){main .items.orange{bottom:0;z-index:1000}}main .items.red{border-color:#ea5353}@media screen and (min-width: 1126px){main .items.red{top:0;z-index:1000}}main .items.green{border-color:#45d3d3}@media screen and (min-width: 1126px){main .items.green{top:0;left:0;bottom:0;margin:auto 0;z-index:1000}}main .items img{position:absolute;display:block;margin:0 0 0 auto;bottom:30px;right:30px}

